@import url("https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;500;600;700&display=swap");
*, *::after, *::before {
  position: relative;
  padding: 0px;
  margin: 0px;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  overflow-x: hidden;
  font-family: "Open Sans", sans-serif;
  background-color: #F9F9F9;
}

.loader {
  z-index: 10;
  position: absolute;
  height: 110vh;
  width: 100vw;
  left: 0px;
}

.loader1 {
  z-index: 9;
  background-color: #fff;
  top: 0vh;
}

.loader2 {
  z-index: 10;
  background: #BB0E29;
  top: 100vh;
}

.mainContainer {
  width: 100%;
  min-height: 105vh;
  padding: 0px 22px;
  background-color: #F9F9F9;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 70px 1fr;
}

@media only screen and (min-width: 480px) {
  .mainContainer {
    padding: 0px 30px;
  }
}
@media only screen and (min-width: 769px) {
  .mainContainer {
    padding: 0px 35px;
  }
}
@media only screen and (min-width: 1024px) {
  .mainContainer {
    padding: 0px;
    width: calc(100% - 100px);
    margin: auto;
    left: 0px;
    right: 0px;
  }
}
@media only screen and (min-width: 1200px) {
  .mainContainer {
    padding: 0px 80px;
  }
}
nav.navMainContainer {
  padding: 50px;
  z-index: 8;
  position: fixed;
  top: 0;
  left: 100%;
  min-height: 430px;
  height: 100%;
  width: 100%;
  background: rgb(187, 14, 41);
  display: grid;
  justify-items: start;
  align-items: center;
  display: none;
}
nav.navMainContainer .navInnerCircle {
  position: absolute;
  left: 50px;
  top: 0px;
  width: 100vh;
  height: 100vh;
  border-radius: 50%;
  background: linear-gradient(to bottom left, rgb(167, 7, 32), rgb(187, 14, 41));
}
nav.navMainContainer button.closeBtn {
  padding: 0px;
  display: block;
  z-index: 999;
  position: absolute;
  right: 50px;
  top: 50px;
  height: 25px;
  width: 25px;
  background: none;
  border: none;
  cursor: pointer;
}
nav.navMainContainer button.closeBtn:active {
  border: none;
  background-color: none;
  outline: none;
}
nav.navMainContainer button.closeBtn::after, nav.navMainContainer button.closeBtn::before {
  content: " ";
  position: absolute;
  height: 2.5px;
  width: 100%;
  top: 50%;
  left: 0px;
  background: #fff;
}
nav.navMainContainer button.closeBtn::after {
  transform: rotate(45deg);
}
nav.navMainContainer button.closeBtn::before {
  transform: rotate(-45deg);
}
nav.navMainContainer ul.navMenu {
  list-style-type: none;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto auto auto;
  grid-row-gap: 18px;
}
nav.navMainContainer ul.navMenu li.navList {
  overflow: hidden;
  cursor: pointer;
  height: auto;
  width: 100%;
  padding: 3px;
  font-size: 22px;
  font-weight: 500;
  color: #fff;
  display: grid;
  grid-template-columns: auto 20px 1fr;
  grid-column-gap: 8px;
}
nav.navMainContainer ul.navMenu li.navList:hover .line {
  transition: all 0.2s ease-in-out;
  width: 30px;
}
nav.navMainContainer ul.navMenu li.navList .line {
  transition: all 0.3s ease-in-out;
  position: relative;
  width: 0px;
  height: 100%;
}
nav.navMainContainer ul.navMenu li.navList .line::after {
  content: " ";
  position: absolute;
  width: 90%;
  top: 30%;
  left: 0px;
  transform: translateY(-50%);
  background: #fff;
  height: 1.5px;
}
nav.navMainContainer ul.navMenu li.navList p.navNumP {
  font-size: 12px;
  display: grid;
  align-items: center;
}
nav.navMainContainer ul.navMenu li.navList p.navP {
  display: grid;
  align-items: start;
}
nav.navMainContainer .navFooter {
  padding: 30px 0px;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: 5px;
  width: calc(100% - 50px - 50px);
  height: auto;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto auto auto;
}
nav.navMainContainer .navFooter p {
  overflow: hidden;
  font-size: 16px;
  color: #fff;
}
nav.navMainContainer .navFooter p span {
  font-size: 24px;
  text-transform: uppercase;
}
nav.navMainContainer .navFooter .firstPFooter {
  font-size: 10px;
}
nav.navMainContainer .navFooter .firstPFooter span {
  font-size: 10px;
}
nav.navMainContainer .navFooter .footerLine {
  position: relative;
  width: 100%;
  height: 20px;
}
nav.navMainContainer .navFooter .footerLine::after {
  content: " ";
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 100%;
  left: 0px;
  height: 1.5px;
}
nav.navMainContainer .navFooter .footerLine .flineFix {
  width: 100%;
  z-index: 2;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  height: 1.5px;
  background: #fff;
}
nav.navMainContainer .navFooter p.lastPFooter {
  justify-self: end;
}

@media only screen and (min-width: 320px) {
  .navFooter p {
    font-size: 16px;
  }
  .navFooter p span {
    font-size: 24px;
    text-transform: uppercase;
  }
  .navFooter .firstPFooter {
    font-size: 10px !important;
  }
  .navFooter .firstPFooter span {
    font-size: 13px !important;
  }
}
@media only screen and (min-width: 480px) {
  .navFooter p {
    font-size: 16px;
  }
  .navFooter p span {
    font-size: 24px;
    text-transform: uppercase;
  }
  .navFooter .firstPFooter {
    font-size: 16px !important;
  }
  .navFooter .firstPFooter span {
    font-size: 24px !important;
  }
}
@media only screen and (min-width: 769px) {
  nav.navMainContainer {
    padding: 70px;
  }
  nav.navMainContainer .navInnerCircle {
    position: absolute;
    left: 70px;
  }
  nav.navMainContainer button.closeBtn {
    right: 70px;
    top: 70px;
  }
  nav.navMainContainer ul.navMenu {
    grid-row-gap: 22px;
  }
  nav.navMainContainer ul.navMenu li.navList {
    font-size: 28px;
    font-weight: 500;
    grid-column-gap: 20px;
  }
  nav.navMainContainer ul.navMenu li.navList .line {
    transition: all 0.3s ease-in-out;
    position: relative;
    width: 0px;
    height: 100%;
  }
  nav.navMainContainer ul.navMenu li.navList p.navNumP {
    font-size: 12px;
    display: grid;
    align-items: center;
  }
  nav.navMainContainer ul.navMenu li.navList p.navP {
    display: grid;
    align-items: start;
  }
  nav.navMainContainer .navFooter {
    bottom: 5px;
    width: calc(100% - 70px - 70px);
  }
}
@media only screen and (max-height: 430px) {
  .navFooter {
    display: none !important;
  }
  nav.navMainContainer {
    padding: 20px;
  }
  nav.navMainContainer .navInnerCircle {
    position: absolute;
    left: 20px;
  }
  nav.navMainContainer button.closeBtn {
    right: 20px;
    top: 20px;
  }
  nav.navMainContainer ul.navMenu {
    grid-row-gap: 10px;
  }
  nav.navMainContainer ul.navMenu li.navList {
    font-size: 18px;
    font-weight: 500;
    grid-column-gap: 20px;
  }
  nav.navMainContainer ul.navMenu li.navList .line {
    transition: all 0.3s ease-in-out;
    position: relative;
    width: 0px;
    height: 100%;
  }
  nav.navMainContainer ul.navMenu li.navList p.navNumP {
    font-size: 12px;
    display: grid;
    align-items: center;
  }
  nav.navMainContainer ul.navMenu li.navList p.navP {
    display: grid;
    align-items: start;
  }
}
header.Header {
  z-index: 7;
  position: fixed !important;
  width: calc(100% - 44px);
  top: 0px;
  left: 50%;
  transform: translateX(-50%);
  background: white;
  height: 70px;
  display: grid;
  grid-template-columns: 1fr auto 1fr;
  align-items: center;
  text-transform: uppercase;
  font-weight: 600;
  position: fixed !important;
}
header.Header::after, header.Header::before {
  content: " ";
  position: absolute;
  top: 0px;
  height: 100%;
  width: 100%;
  background: #fff;
  border-bottom: 1.2px solid #fff;
}
header.Header::after {
  left: -100%;
}
header.Header::before {
  left: 100%;
}
header.Header .HeaderBorderLine {
  position: absolute;
  bottom: -0.5px;
  width: 100%;
  height: 1.2px;
  background: #BFBFBF;
  left: 0%;
}

.H_leftCon {
  color: #000;
}
.H_leftCon ul.ulLogo {
  padding: 0px;
  margin: 0px;
  list-style-type: none;
}
.H_leftCon ul.ulLogo li.liLogo {
  overflow: hidden;
  height: 30px;
  width: auto;
  font-size: 20px;
  display: inline-block;
}
.H_leftCon ul.ulLogo li.liLogo:nth-child(2) {
  color: #DB0626 !important;
}
.H_leftCon ul.ulLogo li.liLogo .logoP {
  font-size: inherit;
  display: grid;
  justify-items: center;
  align-items: center;
}

.H_midCon {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto auto;
  align-content: center;
}
.H_midCon .pMidWrap {
  overflow: hidden;
  height: 5.6vw;
  width: auto;
}
.H_midCon .pMidWrap:nth-child(1) {
  color: #DB0626;
}
.H_midCon .pMidWrap p {
  line-height: 0.98;
  padding: 0px;
  margin: 0px;
  font-size: 5vw;
  height: auto;
  display: grid;
  grid-template-columns: 1fr;
}
.H_midCon .pMidWrap p:first-child {
  color: inherit;
}

.H_rightCon {
  height: auto;
  width: auto;
  justify-self: end;
}
.H_rightCon .menuCon {
  cursor: pointer;
  height: 24px;
  width: 22px;
  overflow: visible;
}
.H_rightCon .menuCon .menuLine {
  right: 0%;
  top: 1px;
  position: absolute;
  height: 2.2px;
  width: 100%;
  background-color: rgb(0, 0, 0);
}
.H_rightCon .menuCon .menuLine:nth-child(2) {
  width: 60%;
  height: 2.4px;
  top: 50%;
  transform: translateY(-50%);
  background-color: rgb(0, 0, 0);
}
.H_rightCon .menuCon .menuLine:last-child {
  top: 100%;
  transform: translateY(-4px);
  background-color: rgb(0, 0, 0);
}

.H_rightprogressStatic {
  position: absolute;
  top: 100%;
  height: calc(100vh - 70px);
  right: 0;
  width: 1px;
}
.H_rightprogressStatic .H_rightprogressContainer {
  overflow: hidden;
  width: 100%;
  height: calc(50vh - 100px);
  position: absolute;
  top: 50%;
  left: 0px;
  transform: translateY(-50%);
  background-color: #BFBFBF;
  border: none;
}
.H_rightprogressStatic .H_rightprogressContainer .H_rightProgress {
  position: absolute;
  left: 0px;
  height: 100%;
  width: 200%;
  background: #fb1537;
  top: -100%;
}
.H_rightprogressStatic .H_rightprogressContainer .H_rightProgressInit {
  position: absolute;
  left: 0px;
  height: 100%;
  width: 200%;
  background-color: #F9F9F9;
  top: -100%;
}

