}

/* INDIVIDUAL CATEGORY SECTION */
.common_cat_conSection {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr;
  grid-auto-rows: auto;
  grid-row-gap: 15vw;
}
.common_cat_conSection .commonCard {
  height: calc(80vw - 18px);
}
.common_cat_conSection .heading_card {
  height: fit-content;
}
.common_cat_conSection .heading_card .common_HeadingCon {
  overflow: hidden;
  width: 100%;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto 20px auto;
  grid-template-areas: "h5" "." "h2";
  /**** FIX ****/
}
.common_cat_conSection .heading_card .common_HeadingCon .initCatHCon {
  overflow: hidden;
  grid-area: h5;
  height: auto;
  width: 100%;
}
.common_cat_conSection .heading_card .common_HeadingCon .initCatHCon h5 {
  color: #DB0626;
  font-weight: 400;
  font-size: 14px;
}
.common_cat_conSection .heading_card .common_HeadingCon .initCatH2Con {
  overflow: hidden;
  height: auto;
  grid-area: h2;
}
.common_cat_conSection .heading_card .common_HeadingCon .initCatH2Con h2 {
  font-size: 30px;
  font-weight: 700;
}
.common_cat_conSection .heading_card .common_HeadingCon h5 {
  grid-area: h5;
  color: #DB0626;
  font-weight: 400;
  font-size: 14px;
}
.common_cat_conSection .heading_card .common_HeadingCon h2 {
  grid-area: h2;
  font-size: 30px;
  font-weight: 700;
}
.common_cat_conSection .commonCard {
  pointer-events: none;
}
.common_cat_conSection .commonCard:hover .cardDescription {
  opacity: 1;
  transition: all 0.3s ease-in;
}
.common_cat_conSection .commonCard .imageCon {
  overflow: hidden;
  height: 100%;
  width: 100%;
}
.common_cat_conSection .commonCard .imageCon .cCardinit {
  z-index: 8;
  background-color: #F9F9F9;
  background-color: #BB0E29;
  position: absolute;
  top: -100%;
  left: 0%;
  height: 100%;
  width: 110%;
  pointer-events: none;
}
.common_cat_conSection .commonCard .imageCon img {
  opacity: 0;
  height: 100%;
  width: 100%;
  object-fit: cover;
  filter: grayscale(100%);
  transition: all 0.3s linear;
}
.common_cat_conSection .commonCard .imageCon img:hover {
  transition: all 0.3s linear;
  transform: scale(1.04);
}
.common_cat_conSection .commonCard .cardDescriptionCon {
  opacity: 0;
  z-index: 5;
  position: absolute;
  bottom: 0px;
  left: 0px;
  height: auto;
  width: 100%;
  padding: 10px;
  max-height: 100%;
  overflow: hidden;
  color: #fff;
}
.common_cat_conSection .commonCard .cardDescriptionCon p {
  opacity: 0;
  font-size: 15px;
  line-height: 15px;
  font-weight: 400;
  transition: all 0.3 ease-in;
}
.common_cat_conSection .commonCard .cardDescriptionCon .initCDCon {
  overflow: hidden;
  height: auto;
  width: 100%;
}
.common_cat_conSection .commonCard .cardDescriptionCon .initCDCon h3 {
  font-size: 19px;
}
.common_cat_conSection .commonCard .cardDescriptionCon h3 {
  font-size: 19px;
}

