.bgScrollTextCon {
  position: fixed;
  z-index: 3;
  top: 55%;
  left: 50%;
  transform: translate(-50%, -50%);
  height: 80%;
  width: 100%;
  display: grid;
  grid-template-columns: 1fr;
  justify-items: center;
