}
.bgScrollTextCon .catHeadingScrollView {
  overflow: hidden;
  width: 100%;
  min-height: 150px;
  display: grid;
  grid-template-columns: 1fr;
  justify-items: center;
  align-items: center;
  justify-content: center;
  align-content: center;
}
.bgScrollTextCon .catHeadingScrollView ul.catScrollHeadings {
  padding: 0px;
  margin: 0px;
  position: absolute;
  width: 100%;
  min-height: 170px;
  height: auto;
  list-style-type: none;
  left: 0px;
  top: 0px;
  display: grid;
  grid-template-columns: 1fr;
  grid-row-gap: 20px;
}
.bgScrollTextCon .catHeadingScrollView ul.catScrollHeadings li.catScrollList {
  position: absolute;
  top: 100%;
  left: 0%;
  width: 100%;
